
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'

import { mapGetters } from 'vuex'
import addressData from '../../common/data/china_address.json'
import occupationData from '../../common/data/occupation.json'

export default {
components: {
Group,
Cell,
Popup,
XInput,
PopupHeader,
XAddress,
PopupPicker,
Datetime
},
data () {
return {
showInputName: false,
addressValue: [],
ChinaAddressData: [],
Birthday: '',
constellationsList: [['白羊座', '金牛座', '双子座', '巨蟹座', '狮子座', '处女座', '天秤座', '天蝎座', '射手座', '摩羯座', '水瓶座', '双鱼座']],
constellationsValue: [],
educationList: [['博士', '硕士', '本科', '大专', '中专', '高中', '初中']],
educationValue: [],
occupationList: [],
occupationValue: []
}
},
methods: {
change (val) {
console.log('on change', val)
},
onEnter (val) {
console.log('click enter!', val)
},
onShadowChange (ids, names) {
console.log(ids, names)
},
occupationHide (type) {
console.log('on hide', type)
if (type) {
this.occupationValue[0] = '--'
}
}
},
computed: {
...mapGetters([
'renterInfo'
])
},
created () {
this.ChinaAddressData = addressData
this.occupationList = occupationData
this.addressValue[0] = this.renterInfo[0].personalInfo.addressProvince
this.addressValue[1] = this.renterInfo[0].personalInfo.addressCity
}
}



